function loading (httpRequest) {
    return {
        data: function () {
            httpRequest.fetchBefore = () => this.showLoading = true
            // httpRequest.fetchAfter = () => this.showLoading = false
            return {
                loading: require('../../media/loading.gif'),
                showLoading: false,
                fetch: httpRequest
            }
        }
    }
}

export default loading



